import React, {Component} from 'react';
import './HeaderLink.css'

export default class HeaderLink extends Component {

    render() {
        const {done, label, elementID, isDone} = this.props;
        const isActiveClass = done ? 'HeaderLink HeaderLink-active' : 'HeaderLink ';

        return (
            <a className={isActiveClass} onClick={(id) => isDone(elementID)}>
                {label}
            </a>
        )
    }
}


