import React, {Component} from 'react';
import HeaderOptions from "../HeaderOption";
import UserInfo from "../UserInfo";
import Address from "../Address";
import CheckBox from "../CheckBox"
import BtGroup from "../BtGroup";
import './app.css'

export default class App extends Component {

    state = {
        data: [
            {label: 'About', id: 1, done: true},
            {label: 'Account', id: 2, done: false},
            {label: 'Address', id: 3, done: false}
        ]
    };

    isDone = (id) => {
        this.setState(({data}) => {
            let shallowCopy = [...data];
            let idx = shallowCopy.findIndex(el => el.id === id);
            let newItem = shallowCopy[idx];
            shallowCopy.map(el => el.done = false);
            shallowCopy[idx] = {
                ...newItem,
                done: !newItem.done
            };

            return {
                data: shallowCopy
            }
        })
    };

    isNext = (e) => {
        let btnText = e.target.innerText;

        switch (btnText) {
            case 'NEXT':
                this.setState(({data}) => {
                    let shallowCopy = [...data];
                    let idx = shallowCopy.findIndex(el => el.done === true);
                    shallowCopy.map(el => el.done = false);
                    if (shallowCopy[idx + 1] === undefined) {
                        shallowCopy[2].done = true
                    } else {
                        shallowCopy[idx + 1].done = true;
                        return shallowCopy
                    }
                });
                break;
            case 'PREVIOUS':
                this.setState(({data}) => {
                    let shallowCopy = [...data];
                    let idx = shallowCopy.findIndex(el => el.done === true);
                    shallowCopy.map(el => el.done = false);
                    shallowCopy[idx - 1].done = true;
                    return shallowCopy
                });
                break;
        }
    };

    render() {
        return (
            <div className="first">
                <div className='main'>
                    <HeaderOptions
                        isDone={(id) => this.isDone(id)}
                        isData={this.state.data}
                    />
                    {this.state.data[0].done ? <UserInfo/> : null}
                    {this.state.data[1].done ? <CheckBox/> : null}
                    {this.state.data[2].done ? <Address/> : null}
                    <BtGroup isData={this.state.data}
                             isNext={(e) => this.isNext(e)}/>
                </div>
            </div>

        );
    };
};