import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Select from "../Select"

import './Adress.css'


const useStyles = makeStyles(theme => ({
    container: {
        display: 'grid',
        gridTemplateColumns: 'repeat(12, 1fr)',
        gridGap: theme.spacing(3),
    },
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        whiteSpace: 'nowrap',
        marginBottom: theme.spacing(4),
    },
    divider: {
        margin: theme.spacing(2, 0),
    },
    root: {
        '& > *': {
            margin: theme.spacing(1),
            width: 200,
        },
    },
}));

export default function Address() {
    const classes = useStyles();

    return (
        <div>
            <div className="AddressTitle">
                Are you living in a nice area?
            </div>
            <div className='AddressInfo'>
                <Grid className={`classes.paper`} item xs={8}>
                    <TextField id="standard-basic" label="Street Name"/>
                </Grid>
                <Grid className={classes.paper} item xs={3}>
                    <TextField id="standard-basic" label="Street No."/>
                </Grid>
                <Grid className={classes.paper} item xs={5}>
                    <TextField id="standard-basic" label="City"/>
                </Grid>
                <Grid className={classes.paper} item xs={6}>
                    <Select/>
                </Grid>
            </div>
        </div>
    );
};