import React from 'react';
import Button from '@material-ui/core/Button';
import './BtGroup.css'

export default function BtGroup({isData, isNext }) {
    const Hidden = isData[0].done ? 'Bt-Hidden' : 'Bt';
    return (
        <div className='ButtonGroup'>
            <Button onClick={(e) => isNext(e)} className={Hidden} variant="contained">Previous</Button>
            <Button onClick={(e) => isNext(e)} className='Bt bt-next' variant="contained" color="secondary">
                Next
            </Button>
        </div>
    )
}