import React, {Component} from "react";
import HeaderLink from '../HeaderLink'
import './HeaderOption.css'

export default class HeaderOption extends Component {

    render() {
        const {isData, isDone} = this.props;

        return (
            <div>
                <div className='HeaderWrapper'>
                    <h3 className='HeaderTitle'>Build Your Profile</h3>
                    <div className='HeaderDescription'>This information will let us know more about you.
                    </div>
                </div>
                <ul className='HeaderItem'>
                    {isData.map(({id, ...otherData}) => {
                        return (
                            <HeaderLink
                                isDone={(id) => isDone(id)}
                                elementID={id}
                                {...otherData}/>
                        )
                    })}
                </ul>
            </div>
        );
    };
};

