import React from "react";
import {makeStyles} from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import './UserInfo.css'

const useStyles = makeStyles(theme => ({
    root: {
        '& > *': {
            marginTop: theme.spacing(2.8),
            width: 350,
            color: '#555'
        },
    },
}));

const UserInfo = () => {
    const classes = useStyles();
    return (
        <div className='UserWrapper'>
            <div className='UserDescription'>Let's start with the basic information (with validation)
            </div>
            <div>
                <div className='UserContainer'>
                    <div className='PictureContainer'>
                        <div className='Picture'>
                            <img
                                src="data:image/png;base64,"
                                className='PictureSrc'/>
                            <input className='InputSrc' type="file"/>
                        </div>
                        <h6 className='PictureDescription'>Choose picture</h6>
                    </div>
                    <div className='InputWrapper'>
                        <div className='LabelContainer'>
                            <TextField id="standard-basic" label="First Name (required)"/>
                            <i className="FirstIcon material-icons md-dark md-inactive">face</i>
                        </div>
                        <div className="LabelContainer">
                            <TextField id="standard-basic" className='LabelContainer' label="Last Name (required)"/>
                            <i className="LastIcon material-icons md-dark md-inactive">account_box</i>
                        </div>
                    </div>
                </div>
                <div className='MailContainer'>
                    <TextField className="MailInput"  type='email' id="standard-basic" label="Email (required)"/>
                    <i className="MailIcon material-icons md-dark md-inactive">email</i>
                </div>
            </div>
        </div>
    );
};

export default UserInfo;