import React from 'react';

import Select from "../Select"
import './CheckBox.css'


export default function CheckBox() {
    return (
        <div>
            <div className='CheckBoxDescription'>
                What are you doing? (checkboxes)
            </div>
            <div className='BoxContainer'>
                <div className='BoxOption'>
                    <div className='InputContainer'>
                        <i className="material-icons icon icon-1">
                            create
                        </i>
                        <input className="InputCheck" id='I-1' tabIndex="-1" type="checkbox" data-indeterminate="false"
                               value=""/>
                        <label className='LabelCheck' htmlFor='I-1'/>
                    </div>
                    <h6 className='IconDescription'>Design</h6>
                </div>
                <div className='BoxOption'>
                    <div className='InputContainer'>
                        <i className="material-icons icon icon-2">
                            keyboard_tab
                        </i>
                        <input className="InputCheck" id='I-2' tabIndex="-1" type="checkbox" data-indeterminate="false"
                               value=""/>
                        <label className='LabelCheck' htmlFor='I-2'/>

                    </div>
                    <h6 className='IconDescription'>Code</h6>
                </div>
                <div className='BoxOption'>
                    <div className='InputContainer'>
                        <input className="InputCheck" id='I-3' tabIndex="-1" type="checkbox" data-indeterminate="false"
                               value=""/>
                        <label className='LabelCheck' htmlFor='I-3'/>
                        <i className="material-icons icon icon-3">
                            desktop_mac
                        </i>
                    </div>
                    <h6 className='IconDescription'>Develop</h6>
                </div>
            </div>
            <div className='SelectBox'>
                <Select/>
            </div>
        </div>
    )
}